import "./index.css";
export default function App() {
  var bgcolor;
  var fontcolor;
  var text = "";
  var time = new Date();
  var hour = time.getHours();
  if (hour > 12 && hour < 5) {
    fontcolor = "black";
    bgcolor = "yellow";
    text = "Breakfast Time!";
  } else if (hour <= 12 && hour <= 18) {
    fontcolor = "blue";
    bgcolor = "pink";
    text = "Lunch Time!";
  } else {
    fontcolor = "red";
    bgcolor = "black";
    text = "Dinner Time!";
  }

  return (
    <>
      <div
        className="time-component"
        style={{ color: fontcolor, backgroundColor: bgcolor }}
      >
        <h1>{text}</h1>
      </div>
    </>
  );
}
